.ConfettiWrapper{
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  z-index: -999;
}

.ConfettiWrapper > div{
  position: absolute;
  left: 50vw;
  bottom: 0;
}

.Congrats{
  margin-top: 2rem;
  width: 100%;
  text-align: center;
  color: #00c853 !important;
}

.congratsSpan{
  font-size: 1.2rem;
  font-weight: bold;
}

.Congrats canvas{
  max-height: 40vh
}

